<template>
  <div style="display: flex;flex-direction: column;">
    <div id="container" style="display: flex; gap: 10px; ">
      <div id="notelist" style="height: 100%; flex: 1;  display: flex; flex-direction: column-reverse;">

        <div v-for="(child, index) in notes" :key="index"
          style="height:12.5%;  display:flex; align-items: center;justify-content: center;  /* 竖直居中 */">
          <span @click="play_effect(index)" class="left-notes" :id="'leftnote' + index"
            :style="{ color: colors[index], fontSize: '200%', 'user-select': 'none' }">
            {{ child }}
          </span>
        </div>

      </div>

      <div id="notepad" style="height: 100%;flex: 8; ">
        <div id="note">
          <div id="note_text">Do</div>
        </div>
      </div>

      <div id="" style="height: 100%;flex: 1; ">
        <div id=""> </div>
      </div>
    </div>


    <div id="" style="top:80vh;width:100%;position: absolute;margin-bottom: 0;display: flex;gap: 10px;  justify-content: center; /* 水平居中 */
  align-items: center; /* 垂直居中 */">
      <button class="niceButton2" id="start_play_btn" @click="start_play" style="margin-top: 0;height: 50px;align-items: center;justify-content: center;">播放</button>
      <button class="niceButton2" id="replay_note_btn" @click="replay_note" style="margin-top: 0;height: 50px;">重播音符</button>

      <div>
        <div class="checkbox-container" style="  display: flex;
            flex-direction: column; /* 设置竖直排列 */
            justify-content: center; /* 竖直居中 */
            align-items: center; /* 水平居中 */
            user-select: none">
          <div style="height: 20px;">
            <input type="checkbox" id="loop_option_btn" @click="loop_option_clicked" checked>
            <label for="loop_option_btn" style="  height: 20px; line-height: 20px; /* 使文本垂直居中 */">循环播放</label>
          </div>

          <div style="height: 20px;">
            <input type="checkbox" id="stop_at_random_option_btn" @click="stop_at_random_option_btn_clicked">
            <label for="stop_at_random_option_btn" style="height: 20px; line-height: 20px; ">在随机音符后暂停</label>
          </div>

        </div>

      </div>
    </div>
  </div>
</template>

<script>

const notes = ['Do', 'Re', 'Mi', 'Fa', 'Sol', 'La', 'Si', 'Do'];
const effects_name = ['C3', 'D3', 'E3', 'F3', 'G3', 'A3', 'B3', 'C4'];
const colors = ['rgb(0,0,0)', 'rgb(146,0,13)', 'rgb(248,15,28)', 'rgb(255,131,33)', 'rgb(20,188,79)', 'rgb(0,164,255)', 'rgb(174,69,179)', 'rgb(146,208,80)'];

let action_chain = [
]

class ActionDealer {
  constructor() {
    this.current_action_index = 0
    this.last_random_note_index = 0
    this.last_note_index = 0
    this.running = false
  }

  replay_note() {
    play_effect(this.last_note_index)
  }

  init_action_chain() {
    action_chain = []

    const noteDiv = document.getElementById('note');
    const notepad = document.getElementById('notepad');

    for (let index = 0; index < notes.length; index++) {
      let xPosition = (notepad.clientWidth) * (index / (notes.length));
      let yPosition = (notepad.clientHeight) * (1 - index / (notes.length)) - noteDiv.clientHeight;

      action_chain.push({ type: 'display-note', note_index: index, x: xPosition, y: yPosition, style: { color: colors[index] } })
      action_chain.push({ type: 'sleep', time: 300 })
    }
    let xCenter = (notepad.clientWidth - noteDiv.clientWidth) / 2;
    let yCenter = (notepad.clientHeight - noteDiv.clientHeight) / 2;

    const notetext = document.getElementById('note_text');
    let origin_fontSize = notetext.style.fontSize
    action_chain = action_chain.concat([
      { type: 'set-notetext-style', style: { fontSize: "300%" } },

      { type: 'display-note', note_index: 0, x: xCenter, y: yCenter, style: { color: colors[0] } },
      { type: 'sleep', time: 1000 },

      { type: 'set-notepad-background-color', color: 'black' },
      { type: 'sleep', time: 300 },
      { type: 'set-notepad-background-color', color: 'white' },

      { type: 'display-note', note_index: 0, x: xCenter, y: yCenter, style: { color: colors[0] } },
      { type: 'sleep', time: 1000 },

      { type: 'set-notepad-background-color', color: 'black' },
      { type: 'display-random-note', note_index: 0, x: xCenter, y: yCenter, style: { color: 'white' } },
      { type: 'sleep', time: 2000 },
      { type: 'set-notepad-background-color', color: 'white' },

      { type: 'redisplay-random-note', x: xCenter, y: yCenter },
      { type: 'sleep', time: 2000 },

      { type: 'set-notetext-style', style: { fontSize: origin_fontSize } },

    ])

  }

  async do_action(action) {
    if (action.type == 'sleep') {
      await sleep(action.time)
    }
    else if (action.type == 'display-note') {
      let x = action.x
      let y = action.y
      play_effect(action.note_index)
      set_note_text(notes[action.note_index])
      set_note_position(x, y)
      set_notetext_style(action.style)
      this.last_note_index = action.note_index
    }
    else if (action.type == 'display-random-note') {
      let x = action.x
      let y = action.y
      this.last_random_note_index = Math.floor(Math.random() * notes.length);
      play_effect(this.last_random_note_index)
      set_note_text('?')
      set_note_position(x, y)
      set_notetext_style(action.style)
      this.last_note_index = this.last_random_note_index
    }
    else if (action.type == 'redisplay-random-note') {
      let x = action.x
      let y = action.y
      play_effect(this.last_random_note_index)
      set_note_text(notes[this.last_random_note_index])
      set_note_position(x, y)
      set_notetext_style({ color: colors[this.last_random_note_index] })
    }
    else if (action.type == 'set-notepad-background-color') {
      set_notepad_bg_color(action.color)
    }
    else if (action.type == 'set-notetext-style') {
      set_notetext_style(action.style)
    }


  }

  async step() {
    await this.do_action(action_chain[this.current_action_index])
  }

  stop() {
    isPlaying = false
    return
  }
  jump_to_next_non_sleep_action() {
    let i = this.current_action_index
    //找到下一个非sleep的action
    for (; i < action_chain.length; i++) {
      if (action_chain[i].type != 'sleep')
        break;
    }
    this.current_action_index = i
  }
  async run() {
    this.running = true
    let i = 0
    while (i < 100) {
      if (!isPlaying) {
        this.jump_to_next_non_sleep_action()
        break;
      }

      i++

      await this.step()
      this.current_action_index++

      if (stop_at_random && action_chain[this.current_action_index - 1].type == "display-random-note") {
        const btn = document.getElementById('start_play_btn');
        btn.click()
        this.jump_to_next_non_sleep_action()
        break
      }

      if (this.current_action_index == action_chain.length) {
        this.current_action_index = 0
        if (!loopMode) {
          const btn = document.getElementById('start_play_btn');
          btn.click()
          break
        }
      }

      if (i == 100)
        i = 0

    }
    this.running = false
  }

}

let ad = new ActionDealer()

let isPlaying = false;
let loopMode = true;
let stop_at_random = false;

import { Howl } from 'howler';

//import $ from 'jquery'
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      notes,
      colors,
      play_effect
    };
  },
  mounted() {


    const noteDiv = document.getElementById('note');
    const leftnode1 = document.getElementById('leftnote1');

    noteDiv.style.fontSize = `${leftnode1.style.fontSize * 150}%`
    noteDiv.style.fontSize = "300%"

    noteDiv.style.height = `${leftnode1.parentElement.clientHeight}px`
    noteDiv.style.width = `${leftnode1.parentElement.clientWidth}px`


    ad.init_action_chain()
  },
  methods: {
    loop_option_clicked() {
      loopMode = !loopMode
    },
    stop_at_random_option_btn_clicked() {
      stop_at_random = !stop_at_random
    },
    replay_note() {
      ad.replay_note()
    },
    async start_play() {
      console.log(isPlaying)
//❚❚三角啊▷▶▕▕▏▏▏▕▕┃┃︱︱︳︳
      const btn = document.getElementById('start_play_btn');
      if (!isPlaying) {
        isPlaying = true
        btn.innerHTML = '暂停⏸️'
        btn.innerHTML = '<div>❚❚</div>'
        btn.innerHTML = '❚❚'
        if (!ad.running) {
          await ad.run()
        }
        console.log('暂停')
      }
      else {
        isPlaying = false
        btn.innerHTML = '播放▶️'
        btn.innerHTML = '<div>►</div>'
        btn.innerHTML = '►'
        ad.stop()
        console.log('播放')
      }
    },

  }
}

let effects = []

for (let index = 0; index < notes.length; index++) {
  var effect_src = "min/" + effects_name[index] + ".mp3";
  var sound = new Howl({
    src: [effect_src],
    volume: 1,
  });
  effects.push(sound)
}

function play_effect(index) {
  effects[index].play();
}

function set_note_position(x, y) {
  const noteDiv = document.getElementById('note');

  noteDiv.style.marginLeft = `${x}px`;
  noteDiv.style.marginTop = `${y}px`;
}

function set_notetext_style(style) {
  const noteDiv = document.getElementById('note_text');

  for (let key in style) {
    noteDiv.style[key] = style[key]
  }

}

function set_note_text(text) {
  const noteDiv = document.getElementById('note_text');
  noteDiv.innerHTML = text
}

/*
function set_note_color(color) {
  const noteDiv = document.getElementById('note');
  noteDiv.style.color=color
}
*/

function set_notepad_bg_color(color) {
  const pad = document.getElementById('notepad');
  pad.style.background = color

}


function sleep(time) {
  return new Promise((resolve) => setTimeout(resolve, time));
}




</script>

